body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  background-color: #7C7C7C;
  position: fixed;
  width: 100%;
  z-index: 500; }
  .navbar .navbar-brand {
    color: #F5DD16 !important; }

.tasks_list {
  margin-top: 70px; }
  .tasks_list .title {
    font-size: 22px;
    color: #7C7C7C;
    font-weight: 600;
    margin-bottom: 4px; }
  .tasks_list .create_task_btn {
    background-color: #7C7C7C;
    border-color: #7C7C7C;
    font-weight: 500;
    margin-top: 10px; }
  .tasks_list .card {
    background-color: #7C7C7C; }
    .tasks_list .card table {
      color: white;
      font-weight: 500;
      margin-bottom: 5px; }
      .tasks_list .card table tbody > tr:nth-child(1) > td {
        border-top: 0; }
      .tasks_list .card table .check {
        color: #F5DD16;
        font-size: 15px; }
      .tasks_list .card table .delete {
        color: white; }

